.cpu {
    display: flex;
    height: 80%;
    align-items: center;
    justify-content: center;
    background-color: var(--color-bg-light);
    border-radius: 1em;
    padding: 0 0.6em;
    box-sizing: border-box;
    
  
    .percentage {
        font-size: 1.2em;
        color: var(--color-text-primary);
        align-self: center;
    }
  
    .iconContainer {
        width: 1.875em;
        height: 1.875em;
        background-color: var(--color-bg-dark);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        margin-left: 0.2em;

        .cpuIcon {
            width: 60%;
            height: 60%;
            font-weight: bold;
            color: var(--color-accent-primary);
        }

        .cpuCircle {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            fill: none;
            stroke: var(--color-accent-primary);
            stroke-width: 0.1875em;
            stroke-linecap: round;
            transform: rotate(-90deg);
            transition: stroke-dashoffset 0.3s ease-in-out;
        }
    }
  }